import { CardType } from "@/components/game-card";
import { multiplyScore, resetScore, stealScore, swapScore, updateScore } from "./firebase";

interface ApplyCardEffectOptions {
    roomCode: string;
    playerId: string;        // ng∆∞·ªùi m·ªü card
    card: CardType;
    targetPlayerId?: string; // khi steal ho·∫∑c swap
  }
  
  // H√†m n√†y √°p d·ª•ng hi·ªáu ·ª©ng c·ªßa card
  export const applyCardEffect = async ({
    roomCode,
    playerId,
    card,
    targetPlayerId,
  }: ApplyCardEffectOptions) => {
    switch (card) {
      case "+50":
        await updateScore(roomCode, playerId, 50);
        break;
      case "+100":
        await updateScore(roomCode, playerId, 100);
        break;
      case "+150":
        await updateScore(roomCode, playerId, 150);
        break;
      case "-50":
        await updateScore(roomCode, playerId, -50);
        break;
      case "-100":
        await updateScore(roomCode, playerId, -100);
        break;
      case "-150":
        await updateScore(roomCode, playerId, -150);
        break;
      case "x2":
        await multiplyScore(roomCode, playerId, 2);
        break;
      case "x3":
        await multiplyScore(roomCode, playerId, 3);
        break;
      case "loseAll":
        await resetScore(roomCode, playerId);
        break;
      case "steal20":
        if (targetPlayerId) await stealScore(roomCode, playerId, targetPlayerId, "STEAL_20");
        break;
      case "steal50":
        if (targetPlayerId) await stealScore(roomCode, playerId, targetPlayerId, "STEAL_50");
        break;
      case "swap":
        if (targetPlayerId) await swapScore(roomCode, playerId, targetPlayerId);
        break;
      default:
        break;
    }
  };